import React from "react";
import "./Calc.css";

class Calc extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      result: 0,
    };
  }

  static getDerivedStateFromProps(props, state) {
    return { rate: props.rate };
  }

  calcRate = (e) => {
    e.preventDefault();
    let elements = e.target.elements;
    let countCurrency = elements["type-count"].value;
    let typeCurrency = elements["type-currency"].value;
    this.setState({
      result: (countCurrency / this.state.rate[typeCurrency]).toFixed(2),
    });
  };

  render() {
    return (
      <div className="calculator">
        <h3>Calculator of exchange currency</h3>
        <div className="block">
          <div>I want to buy</div>
          <div>
            <form action="" onSubmit={this.calcRate}>
              <input type="number" defaultValue="150" name="type-count" />

              <select name="type-currency" id="">
                {Object.keys(this.props.rate).map((keyName) => (
                  <option key={keyName} value={keyName}>
                    {keyName}
                  </option>
                ))}
              </select>
              <input type="submit" defaultValue="calc" />
            </form>
          </div>
          <div>
            <h4>Result</h4>
            <ul className="calc-res">
              <li>
                You need to pay <strong>USD : {this.state.result}</strong>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Calc;
